"use client";

import React, { useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { Loader2, Trash2 } from "lucide-react";
import { useCart } from "@/components/cart/CartProvider";
import { Button } from "@/components/ui/Button";
import { Card } from "@/components/ui/Card";
import { formatCurrency } from "@/lib/utils";
import { motion, AnimatePresence } from "framer-motion";

export default function CartPage() {
    const { items, removeItem, updateQuantity, cartTotal, isLoading } = useCart() as any; // Temporary type cast until provider types are perfect
    const [isCheckingOut, setIsCheckingOut] = useState(false);

    const handleCheckout = async () => {
        setIsCheckingOut(true);
        // Simulate API call for checkout link generation
        setTimeout(() => {
            setIsCheckingOut(false);
            // In a real app, this would redirect to a specific Razorpay payment link 
            // or open the Razorpay modal for the total amount
            alert("Redirecting to Razorpay checkout...");
        }, 1500);
    };

    if (items.length === 0) {
        return (
            <div className="min-h-[60vh] flex flex-col items-center justify-center p-4">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="text-center space-y-4"
                >
                    <div className="w-24 h-24 bg-surface-elevated rounded-full flex items-center justify-center mx-auto mb-6">
                        <Loader2 className="w-10 h-10 text-text-tertiary animate-spin" />
                    </div>
                    <h1 className="text-3xl font-display font-bold">Your cart is empty</h1>
                    <p className="text-text-secondary max-w-md mx-auto">
                        Looks like you haven't added anything to your cart yet. Explore our premium collection using the link below.
                    </p>
                    <Link href="/products" passHref>
                        <Button size="lg" className="mt-4">
                            Start Shopping
                        </Button>
                    </Link>
                </motion.div>
            </div>
        );
    }

    return (
        <div className="container-custom py-12">
            <h1 className="text-3xl font-display font-bold mb-8">Shopping Cart</h1>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
                {/* Cart Items */}
                <div className="lg:col-span-2 space-y-6">
                    <AnimatePresence>
                        {items.map((item: any) => (
                            <motion.div
                                key={item.id}
                                layout
                                initial={{ opacity: 0, x: -20 }}
                                animate={{ opacity: 1, x: 0 }}
                                exit={{ opacity: 0, x: -20, height: 0, marginBottom: 0 }}
                                className="group"
                            >
                                <Card className="flex flex-col sm:flex-row gap-6 p-4 items-center">
                                    <div className="relative w-full sm:w-32 h-32 rounded-lg overflow-hidden bg-surface-elevated flex-shrink-0">
                                        {item.image_url ? (
                                            <Image
                                                src={item.image_url}
                                                alt={item.name}
                                                fill
                                                className="object-cover"
                                            />
                                        ) : (
                                            <div className="w-full h-full flex items-center justify-center text-text-tertiary bg-surface-elevated">
                                                No Image
                                            </div>
                                        )}
                                    </div>

                                    <div className="flex-grow space-y-2 text-center sm:text-left">
                                        <h3 className="font-semibold text-lg">{item.name}</h3>
                                        <p className="text-primary font-medium">
                                            {formatCurrency(item.price)}
                                        </p>
                                    </div>

                                    <div className="flex items-center gap-4">
                                        <div className="flex items-center bg-surface-elevated rounded-lg p-1">
                                            <button
                                                onClick={() => updateQuantity(item.id, item.quantity - 1)}
                                                className="w-8 h-8 flex items-center justify-center rounded-md hover:bg-white transition-colors"
                                                disabled={item.quantity <= 1}
                                            >
                                                -
                                            </button>
                                            <span className="w-10 text-center font-medium">
                                                {item.quantity}
                                            </span>
                                            <button
                                                onClick={() => updateQuantity(item.id, item.quantity + 1)}
                                                className="w-8 h-8 flex items-center justify-center rounded-md hover:bg-white transition-colors"
                                            >
                                                +
                                            </button>
                                        </div>

                                        <button
                                            onClick={() => removeItem(item.id)}
                                            className="p-2 text-text-tertiary hover:text-error hover:bg-red-50 rounded-full transition-colors"
                                            aria-label="Remove item"
                                        >
                                            <Trash2 className="w-5 h-5" />
                                        </button>
                                    </div>
                                </Card>
                            </motion.div>
                        ))}
                    </AnimatePresence>
                </div>

                {/* Order Summary */}
                <div className="lg:col-span-1">
                    <Card className="p-6 sticky top-24">
                        <h2 className="text-xl font-semibold mb-6">Order Summary</h2>

                        <div className="space-y-4 mb-6">
                            <div className="flex justify-between text-text-secondary">
                                <span>Subtotal</span>
                                <span>{formatCurrency(cartTotal)}</span>
                            </div>
                            <div className="flex justify-between text-text-secondary">
                                <span>Shipping</span>
                                <span className="text-success font-medium">Free</span>
                            </div>
                            <div className="border-t border-border pt-4 flex justify-between font-bold text-lg">
                                <span>Total</span>
                                <span>{formatCurrency(cartTotal)}</span>
                            </div>
                        </div>

                        <Button
                            size="lg"
                            className="w-full"
                            onClick={handleCheckout}
                            isLoading={isCheckingOut}
                        >
                            Proceed to Checkout
                        </Button>

                        <p className="mt-4 text-xs text-center text-text-tertiary">
                            Secure checkout powered by Razorpay.
                            <br />
                            Encryption standard 256-bit SSL.
                        </p>
                    </Card>
                </div>
            </div>
        </div>
    );
}
